.dropbtn {
  display: none;
}

.dropdown-content p:hover {
  background-color: rgb(75, 73, 73);
  border: 1px solid white; 
  border-radius: 5px;
}

.dropdown-content {
  font-size: 20px;
}

.dropdown-content p {
  color:white;
  width: 40%;
  margin: auto;
  padding: 5px 10px;
  text-decoration: none;
  display: block;
  /* border: 2px solid white; 
  border-radius: 5px; */
}




@media only screen and (max-width: 600px) {
.dropbtn {
  display: block;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 65px;
  height: 45px;
  cursor: pointer;
  margin: 10px 20px;

}

.dropdown {
  position: relative;
  display: inline-block;
  background-color:rgb(71, 67, 67);
  /* background-color: #3a3f3b;  */
  
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color:rgb(71, 67, 67);
  /* background-color: #6b6d6b;  */
  min-width: 100px;
  padding-right: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  
  z-index: 1;
}

.dropdown-content p {
  width: 80%;
  color:white;
  padding: 5px 10px;
  text-decoration: none;
  display: block;
}

.dropdown-content p:hover {
  background-color: rgb(102, 102, 102);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: rgb(71, 67, 67);
}
}